.container {
  display: flex;
  position: absolute;
  width: 100%;
  height: calc(100% - 60px);
  overflow: hidden;
}
.listOfUsers {
  width: 30%;
  height: 100%;
  overflow-x: hidden;
  border-right: 1px solid #ccc;
}
.chatArea {
  width: 70%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
}
.chatHeader {
  position: fixed;
  width: 70%;
  height: 40px;
  background: #e2e0e0;
  text-align: center;
  line-height: 40px;
  color: #000;
  font-weight: bold;
  font-family: sans-serif;
}
.chatControls {
  display: flex;
  position: fixed;
  width: 70%;
  height: 50px;
  bottom: 0;
}
.chatControls textarea {
  width: 90%;
}
.chatControls button {
  width: 10%;
}

.displayName {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.displayName.active {
  background: #ccc;
}

.displayName .displayPic {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 25px;
}

.displayName .displayPic img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.messageStyle {
  background: skyblue;
  display: inline-block;
  padding: 5px 10px;
  border-radius: 10px;
  margin: 5px;
  display: inline-block;
}

.messageSections {
  height: calc(100% - 90px);
  width: 100%;
  overflow-x: hidden;
  position: relative;
  top: 40px;
}
